<template>
  <v-container fluid>
    <v-toolbar flat color="white">
      <v-toolbar-title class="green--text">My Reservations</v-toolbar-title>
      <v-divider
        class="mx-2"
        inset
        vertical
      ></v-divider>
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
      <v-dialog v-model="dialog" max-width="800px">
        <v-btn slot="activator" color="green lighten-1" dark class="mb-2">New Reservation</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ formTitle }}</span>
          </v-card-title>

          <v-card-text>
            <v-container grid-list-md>
              <v-layout wrap>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.guestName" label="Guest Name"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.bookingId" label="Booking ID"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.phoneNo" label="Phone Number"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.email" label="Email"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.memberCount" label="Member Count"></v-text-field>
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-menu
                    :close-on-content-click="false"
                    v-model="menuCheckInDate"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="editedItem.checkInDate"
                      label="Check-in Date"
                      persistent-hint
                      prepend-icon="event"
                      readonly
                    ></v-text-field>
                    <v-date-picker v-model="editedItem.checkInDate" no-title @input="menuCheckInDate = false"></v-date-picker>
                  </v-menu>
                  <!-- <v-text-field v-model="editedItem.checkInDate" label="Check-in Date"></v-text-field> -->
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-menu
                    :close-on-content-click="false"
                    v-model="menuCheckOutDate"
                    :nudge-right="40"
                    lazy
                    transition="scale-transition"
                    offset-y
                    full-width
                    max-width="290px"
                    min-width="290px"
                  >
                    <v-text-field
                      slot="activator"
                      v-model="editedItem.checkOutDate"
                      label="Check-out Date"
                      persistent-hint
                      prepend-icon="event"
                      readonly
                    ></v-text-field>
                    <v-date-picker v-model="editedItem.checkOutDate" no-title @input="menuCheckOutDate = false"></v-date-picker>
                  </v-menu>


                  <!-- <v-text-field v-model="editedItem.checkOutDate" label="Check-out Date"></v-text-field> -->
                </v-flex>
                <v-flex xs12 sm6 md6 lg6>
                  <v-text-field v-model="editedItem.keyInfo" label="Key Info"></v-text-field>
                </v-flex>
              </v-layout>
            </v-container>
          </v-card-text>

          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="green lighten-1" flat @click="close">Cancel</v-btn>
            <v-btn color="green lighten-1" flat @click="save">Save</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>


      <v-dialog v-model="dialogUpload" max-width="800px">
        <v-btn slot="activator" color="green lighten-1" dark class="mb-2">Import Reservations</v-btn>
        <v-card>
          <v-card-title>
            <span class="headline">{{ uploadTitle }}</span>
          </v-card-title>

          <v-card-text>
            <file-upload @filePicked="onFilePick"></file-upload>
          </v-card-text>
        </v-card>
      </v-dialog>
    </v-toolbar>
    <v-data-table
      :headers="headers"
      :items="reservations"
      :pagination.sync="pagination"
      :search="search"
      class="elevation-1"
    >
      <template slot="items" slot-scope="props">
        <td class="text-xs-left">
          <v-icon
            medium
            class="mr-2"
            color="green lighten-1"
            @click="editItem(props.item)"
          >
            edit
          </v-icon>
        </td>
        <td class="text-xs-left">{{ props.item.guestName }}</td>
        <td class="text-xs-left">{{ props.item.bookingId }}</td>
        <td class="text-xs-left">{{ props.item.phoneNo }}</td>
        <td class="text-xs-left">{{ props.item.email }}</td>
        <td class="text-xs-left">{{ props.item.memberCount }}</td>
        <td class="text-xs-left">{{ props.item.checkInDate }}</td>
        <td class="text-xs-left">{{ props.item.checkOutDate }}</td>
        <td class="text-xs-left">{{ props.item.keyInfo }}</td>
        <td class="text-xs-right">
          <v-icon
            medium
            class="mr-2"
            color="green lighten-1"
            @click="pickReservation(props.item)"
          >
            dehaze
          </v-icon>
        </td>
      </template>
    </v-data-table>
  </v-container>
</template>

<style scoped>
  table.v-table thead tr th {
    font-size: 20px;
  }
  table.v-table tbody tr td {
    font-size: 20px;
  }
</style>


<script>
import { mapState, mapGetters, mapActions } from 'vuex'  
import { CallDialog } from '@/utils'
import store from '@/store'
const FileUpload = () => import('@/components/FileUpload.vue')

export default {
  data: () => ({
    menuCheckInDate: false,
    menuCheckOutDate: false,
    search: '',
    dialogUpload: false,
    dialog: false,
    headers: [
      { text: 'Edit', value: 'reservationCode', align: 'left', sortable: false },
      { text: 'Guest Name', value: 'guestName', align: 'left', sortable: true},
      { text: 'Booking ID', value: 'bookingId', align: 'left', sortable: true},
      { text: 'Phone No.', value: 'phoneNo', align: 'left', sortable: false},
      { text: 'Email', value: 'email', align: 'left', sortable: false},
      { text: 'Member Count', value: 'memberCount', align: 'left', sortable: true },
      { text: 'Check-in Date', value: 'checkInDate', align: 'left', sortable: true},
      { text: 'Check-out Date', value: 'checkOutDate', align: 'left', sortable: true},
      { text: 'Key Info', value: 'keyInfo', align: 'left', sortable: false},
      { text: 'Check Members', value: 'reservationCode', align: 'right', sortable: false }    
      ],
    editedIndex: -1,
    editedItem: {
      reservationCode: '',
      guestName: '',
      bookingId: '',
      phoneNo: '',
      email: '',
      memberCount: 0,
      checkInDate: '',
      checkOutDate: '',
      keyInfo: ''
    },
    defaultItem: {
      reservationCode: '',
      guestName: '',
      bookingId: '',
      phoneNo: '',
      email: '',
      memberCount: 0,
      checkInDate: '',
      checkOutDate: '',
      keyInfo: ''
    },
    pagination: {
      descending: true,
      page: 1,
      rowsPerPage: 25,
      sortBy: 'registeredOn',
      totalItems: 0,
      rowsPerPageItems: [10,25,{"text":"$vuetify.dataIterator.rowsPerPageAll","value":-1}]
    },
  }),
  components: {
    FileUpload,
  },
  computed: {
    formTitle () {
      return this.editedIndex === -1 ? 'New Reservation' : 'Edit Reservation'
    },
    uploadTitle () {
      return 'Import Reservations'
    },
    disabled () {
      return this.editedIndex === -1 ? false : true
    },
    ...mapState({
      reservations: state => state.host.reservations,
      listingId: state => state.host.listingId,
      listingName: state => state.host.listingName
    }),
  },

  watch: {
    dialog (val) {
      val || this.close()
    },
    dialogUpload (val) {
      val || this.closeUpload()
    }    
  },
  async beforeRouteEnter (to, from, next) {
    const error = await store.dispatch('host/loadReservations', store.state.host.listingId).catch(error => {
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  async beforeRouteUpdate (to, from, next) {
    const error = await store.dispatch('host/loadReservations', store.state.host.listingId).catch(error => {      
      return error
    })

    if (error) {
      CallDialog(error)
    } else {
      next()
    }
  },
  methods: {
    async onFilePick(file) {
      const error = await this.uploadJson({ fileObject: file.file }).catch(error => {
        console.log('uploadJson', error)
        return error
      })

      if (error) {
        CallDialog(error)
      }



    },
    editItem (item) {   
      this.editedIndex = this.reservations.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },

    deleteItem (item) {
      const index = this.reservations.indexOf(item)
    },
    closeUpload () {
      this.dialogUpload = false
    },
    close () {
      this.editedItem = Object.assign({}, this.defaultItem)
      this.editedIndex = -1
      this.dialog = false
    },
    async save () {

      if (this.editedIndex > -1) {
        
        const error = await this.updateReservation({
          params: this.editedItem,
          index: this.editedIndex
        }).catch(e => {
          console.error('updateReservation', e)
          return e
        })

        if (error) {
          CallDialog(error)  
        } else {
          this.close()
        }
      } else {
        // this.editedItem.listingId = this.listingId
        // this.editedItem.listingName = this.listingName

        // console.log('generateReservation', this.editedItem)

        const error = await this.generateReservation(this.editedItem).catch(e => {
          console.error('generateReservation', e)
          return e
        })

        if (error) {
          CallDialog(error)  
        } else {
          this.close()
        }
      }
    },
    ...mapActions({
      loadReservations: 'host/loadReservations',
      pickReservation: 'host/pickReservation',
      updateReservation: 'host/updateReservation',
      generateReservation: 'host/generateReservation',
      uploadJson: 'host/uploadJson'
    })
  }
}
</script>